import React from 'react';
import PropTypes from 'prop-types';


class Input extends React.Component {
    _handleKeyPress = (e) => {
        if (e.key === 'Enter') {
            this.props.onAdd(e.target.value);
            e.target.value = "";
        }
    }

    _handleButtonPress = () => {
        this.props.onAdd(this.refs.name.value);
        this.refs.name.value = "";
    }

    _handleJsonPress = () => {
        // console.log(this.props.state);
        console.log("JSON print: ", JSON.stringify(this.props.state, null, 2));
        // window.alert(JSON.stringify(this.props.state, null, 2));

        var textArea = document.createElement("textarea");
        textArea.style.position = 'fixed';
        textArea.style.top = 0;
        textArea.style.left = 0;
        textArea.style.width = '2em';
        textArea.style.height = '2em';
        textArea.style.padding = 0;
        textArea.style.border = 'none';
        textArea.style.outline = 'none';
        textArea.style.boxShadow = 'none';
        textArea.style.background = 'transparent';
        textArea.value = JSON.stringify(this.props.state);
        document.body.appendChild(textArea);
        textArea.select();
        try {
            var successful = document.execCommand('copy');
            var msg = successful ? 'successful' : 'unsuccessful';
            console.log('Copying text command was ' + msg);
        } catch (err) {
            console.log('Oops, unable to copy');
        }

        document.body.removeChild(textArea);
    }

    render() {
        return (
            <div>
                <input
                    ref='name'
                    type="text"
                    onKeyPress={this._handleKeyPress}/>
                <button onClick={ this._handleButtonPress }>
                    Add Person (Enter)
                </button>
                <button onClick={ this.props.onClear }>
                    Clear
                </button>
                <button onClick={ this._handleJsonPress }>
                    JSON
                </button>
            </div>
        )
    }
}
Input.propTypes = {
    onAdd: PropTypes.func.isRequired,
    onClear: PropTypes.func.isRequired,
    state: PropTypes.array
};

export default Input;



